/* theme css */

@primary-background: green;
@secondary-color: red;
@dark-color: white;
@dark-background: darken(@primary-background, 30%);
@middle-background: darken(@primary-background, 10%);
@text-muted: desaturate(lighten(@primary-background, 20%), 60%);

/* navbar css */

@navbar-active: darken(@primary-background, 15%);
@navbar-hover: lighten(@primary-background, 10%);

/* btn-primary css */

@btn-primary-color: white;
@btn-primary-background: @primary-background;
@btn-primary-hover: darken(@primary-background, 10%);

/* btn-default css */

@btn-default-color: black;
@btn-default-background: white;
@btn-default-hover: darken(@btn-default-background, 10%);

